import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import vk_cofe from 'vkcoffee'

class Main extends React.Component{

    constructor(props){
        super(props)

        this.state={encode:"",
                    decode:""}
    }

    decode(params) {
        return vk_cofe.decrypt(params);
    }

    render() {
        return(
            <MainA />
        );
    }
}

class MainA extends React.Component{

    constructor(props){
        super(props)

        this.state={encoded:"",
                    decoded:"",
                    password:"",
                    language:true}
    }

    decode(params) {
        let a="";
        if(this.state.password){
            a=vk_cofe.decrypt(params, +this.state.password);
        }else{
            a=vk_cofe.decrypt(params);
        }

        //const d=>;
        
        this.setState({encoded: params, decoded: a});
    }

    render(){
        if(this.state.language){
            return(
            <div className="text-center d-flex flex-row cover-container mx-auto" style={{height: "100%"}}>
                <div className="cover-container d-flex w-100 h-100 p-3 mx-auto flex-column MyDiv">

                    <header className="masthead mb-auto flex-column">
                        <h3 className="masthead-brand float-right" onClick={(e)=>this.setState({language: !this.state.language})}>English</h3>
                    <div className="justify-content-center">
                        <nav className="nav nav-masthead">
                        <a className="nav-link active" >Все привет</a>
                        </nav>
                    </div>
                    </header>
                
                    <main role="main" className="inner cover d-flex flex-column bd-highlight">
                        <span>
                            Тебе надо просто скопировать сюда зашифрованый текст
                        </span>
                        <div>
                        <svg width="5em" height="5em" viewBox="0 0 16 16" className="bi bi-arrow-down-short float-none" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd" d="M8 4a.5.5 0 0 1 .5.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5A.5.5 0 0 1 8 4z"/>
                        </svg></div>
                        <div className="">
                    <textarea className="MytextArea" onChange={(e) => {
                        this.decode(e.target.value);
                        e.target.style.height = "5px";e.target.style.height = (e.target.scrollHeight)+"px";}
                        } value={this.state.encoded} placeholder="VK CO FF EE 72 69 6D 4F 6A 43 37 6E 56 52 4B 31 73 67 37 78 30 46 61 6A 7A 42 57 6C 67 76 6D 44 42 30 34 44 62 78 76 69 4F 52 72 36 68 53 45 3D VK CO FF EE"/>
                    </div>
                    <div>
                        <button className="btn btn-outline-primary" onClick={(e)=>{this.setState({encoded:"", decoded:""})}}>Очистить</button>
                        <input type="number" className="MyPassword" placeholder="Пароль" value={this.state.password} onChange={(e)=>{this.setState({password: e.target.value})}}/>
                    </div>
                    <span className="my" dangerouslySetInnerHTML={{ __html: this.state.decoded?this.state.decoded:'Сдесь будет декодированое сообщение' }}></span>
                    </main>
                
                    <footer className="mastfoot mt-auto">
                    <div className="inner">
                        <h2>vk coffee decoder</h2>
                    </div>
                    </footer>
                </div>

            </div>
        );
        }else{
        return(
            <div className="text-center d-flex flex-row cover-container mx-auto" style={{height: "100%"}}>
                <div className="cover-container d-flex w-100 h-100 p-3 mx-auto flex-column MyDiv">

                    <header className="masthead mb-auto flex-column">
                        <h3 className="masthead-brand float-right" onClick={(e)=>this.setState({language: !this.state.language})}>Русский язык</h3>
                    <div className="justify-content-center">
                        <nav className="nav nav-masthead">
                        <a className="nav-link active" >Hello everyone</a>
                        </nav>
                    </div>
                    </header>
                
                    <main role="main" className="inner cover d-flex flex-column bd-highlight">
                        <span>
                            You just have to pust you code bown there
                        </span>
                        <div>
                        <svg width="5em" height="5em" viewBox="0 0 16 16" className="bi bi-arrow-down-short float-none" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd" d="M8 4a.5.5 0 0 1 .5.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5A.5.5 0 0 1 8 4z"/>
                        </svg></div>
                        <div className="">
                    <textarea className="MytextArea" onChange={(e) => {
                        this.decode(e.target.value);
                        e.target.style.height = "5px";e.target.style.height = (e.target.scrollHeight)+"px";}
                        } value={this.state.encoded} placeholder="VK CO FF EE 72 69 6D 4F 6A 43 37 6E 56 52 4B 31 73 67 37 78 30 46 61 6A 7A 42 57 6C 67 76 6D 44 42 30 34 44 62 78 76 69 4F 52 72 36 68 53 45 3D VK CO FF EE"/>
                    </div>
                    <div>
                        <button className="btn btn-outline-primary" onClick={(e)=>{this.setState({encoded:"", decoded:""})}}>clear</button>
                        <input type="number" className="MyPassword" placeholder="Password" value={this.state.password} onChange={(e)=>{this.setState({password: e.target.value})}}/>
                    </div>
                    <span className="my" dangerouslySetInnerHTML={{ __html: this.state.decoded?this.state.decoded:'There will be your decoded massage' }}></span>
                    </main>
                
                    <footer className="mastfoot mt-auto">
                    <div className="inner">
                        <h2>vk coffee decoder</h2>
                    </div>
                    </footer>
                </div>

            </div>
        );}
    }
}

ReactDOM.render(
    <MainA />,
    document.getElementById('root')
  );